.musicians-list-components {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
}

.musicians-list-container {

    flex-direction: row;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    padding: 3em 2em;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background: url(../../assets/amps-yellow.jpg);
filter: grayscale(25%);
text-shadow: #c4b180;
} 
div.card-container{
background: url(../../assets/amps-yellow.jpg);
background-repeat: no-repeat;
background-size: cover;
}
.musician-div-container {
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 28px;

  font-weight: 500;
  /* border-radius: 22px; */
}

.musicians-container {
  flex-basis: 5%;
  padding: 5px;
}

.musician-name {
  border-radius: 3px solid black;
  padding: 4px;
  height: 35px;
  color: #c4b180;
  font-size: 14pt;
  text-align: center;
  text-decoration: none;
}

.musician-name:hover {
  transition: 0.4s ease-in;
  color: #f7d08f;
  cursor: pointer;
}
div.musician-div-container{}
