/*#FFF1E6;#9CF6FB,#E1FCFD,#394F8A,#4A5FC1,#E5B9A8,#EAD6CD*/
#delete-musician-btn {
  text-align: center;
  width: auto;
  height: 35px;
  border-radius: 6px;
  background-color: white;
  border: none;
  margin: 8px;
font-weight: 700;
}
#delete-musician-btn:hover {
  transition: all 0.4s ease 0s;
  background-color:#c3b17f;
font-weight: 700;
  color: white;
  cursor: pointer;
  border: none;
}
