#add-song-text {
  text-align: center;
  width: auto;
  height: 35px;
  border: 6px;
  border-radius: 6px;
  font-size: 12pt;
  font-weight: 700;
}

#add-song-text:hover {
  transition: all 0.4s ease 0s;
  background-color: #c4b180;
  color: white;
  cursor: pointer;
  border: transparent;
}

div.form-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  background-color: rgb(247, 247, 248);
  padding: 20px;

}

input[type="file"]::file-selector-button {
  margin-right: 20px;
  border: none;
  background: white;
  background-color: #ffffff;
  padding: 10px 20px;
  border-radius: 6px;
  color: #c4b180;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button#create-song-btn {
  width: 75px;
  height: 30px;
  border-radius: 6px;
  background-color: white;
  /*#a5a58d;*/
  font-size: 16px;
  font-weight: 600;
  color: #c3b17f;
  border: none;
  cursor: pointer;
}

button#create-song-btn:hover {
  transition: 0.4 ease in 0.2s;
  background-color: #c3b17f;
  color: white;
}

